.authForm_title{
  font-size: 80px;
  font-family: 'Dancing Script', cursive;
  margin-bottom: 30px;
}

.AuthForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.AuthForm input {
  margin-top: 9px;
  width: 300px;

  border: 1px solid black;
}

/* Nweet */
.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet {
  margin-bottom: 20px;
  background-color: white;
  min-width: 300px;
  max-width: 300px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.nweet h4 {
  font-size: 14px;
 
}

.nweet img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

.Nweet_profile{
 
   font-size: 13px;
   margin-bottom: 15px;
}
